
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}
html {
    height: 100%;
}
body {
    background-color: silver;
    margin: 0;
    padding: 0;
    background: url(/static/img/bg.6ce6d51.jpg);
    background-size: cover;
    background-repeat: repeat;
    -webkit-animation: 2s ease;
            animation: 2s ease;
}
nav {
    background-color: black;
    color: white;
    height: 9.5vh;
    padding: 1vh;
    margin-top: -3.5vh;
}
nav>ul>li>a, footer>a {
      color: white;
      -webkit-transition: color 2s;
      transition: color 2s;
}
nav>ul>li>a:hover, footer>a:hover {
    color: yellow;
    -webkit-transition: color 2s;
    transition: color 2s;
}
li {
    border-bottom: 5px solid darkgrey;
    list-style: none;
    color: white;
    -webkit-transition: border-bottom 2s;
    transition: border-bottom 2s;
}
li:hover {
    border-bottom: 5px solid yellow;
    color: yellow;
    -webkit-transition: border-bottom 2s;
    transition: border-bottom 2s;
}
ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    color: white;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    text-align: center;
    margin: 0 auto;
    font-size: 1.5rem;
}
#about {
    text-align: center;
    color: black;
    background-color: gray;
    width: 35vw;
    min-width: 300px;
    -webkit-box-shadow: 10px 10px;
            box-shadow: 10px 10px;
    margin: 20px auto;
    padding: 20px;
    -webkit-transition: -webkit-box-shadow 2s;
    transition: -webkit-box-shadow 2s;
    transition: box-shadow 2s;
    transition: box-shadow 2s, -webkit-box-shadow 2s;
}
#about:hover {
    -webkit-box-shadow: 10px 10px gold;
            box-shadow: 10px 10px gold;
    -webkit-transition: -webkit-box-shadow 2s;
    transition: -webkit-box-shadow 2s;
    transition: box-shadow 2s;
    transition: box-shadow 2s, -webkit-box-shadow 2s;
}
.fa {
    -webkit-transform: scaleX(1) rotate(0deg);
            transform: scaleX(1) rotate(0deg);
    -webkit-transition: -webkit-transform 2s;
    transition: -webkit-transform 2s;
    transition: transform 2s;
    transition: transform 2s, -webkit-transform 2s;
}
.fa:hover {
    -webkit-transform: scaleX(-1) rotate(180deg);
            transform: scaleX(-1) rotate(180deg);
    -webkit-transition: -webkit-transform 2s;
    transition: -webkit-transform 2s;
    transition: transform 2s;
    transition: transform 2s, -webkit-transform 2s;
}
a {
      text-decoration: none;
      color: black;
      cursor: pointer;
}
.fab {
      -webkit-animation: scroll 2s ease infinite;
              animation: scroll 2s ease infinite;
}
@-webkit-keyframes scroll {
0% { -webkit-transform: scale(1); transform: scale(1)
}
50% { -webkit-transform: scale(1.5); transform: scale(1.5)
}
100% { -webkit-transform: scale(1); transform: scale(1)
}
}
@keyframes scroll {
0% { -webkit-transform: scale(1); transform: scale(1)
}
50% { -webkit-transform: scale(1.5); transform: scale(1.5)
}
100% { -webkit-transform: scale(1); transform: scale(1)
}
}
img {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}
#crud {
    width: 90vw;
    min-height: 89vh;
    background-color: gray;
    margin: 1vh auto;
}
footer {
    background-color: black;
    color: white;
    width: 100vw;
    height: 3vh;
    position: absolute;
    bottom: 0;
    font-size: 1rem;
